import { Component } from 'react';
import FormValidator from "../services/form_validator";
import USER_FORM_RULES from "../validators/user_form_rules";
import ACCOUNT_FORM_RULES from "../validators/account_form_rules";
import AccountType from "./account_type";

export default class SignupForm extends Component {
  constructor(props) {
    super(props);

    this.validator = new FormValidator(user_form_rules.concat(account_form_rules));

    this.state = {
      user: {
        name: '',
        email: '',
        password: '',
        password_confirmation: ''
      },
      account: {
        name: '',
        email: '',
        phone: '',
        account_type_id: 0
      },
      condo: {
        name: ''
      },
      units: {
        qty: 0
      }
    }
  }

  handleInputChange = event => {
    event.preventDefault();

    this.setState({
      [event.target.name]: event.target.value,
    });
  };

  handleAccountType = accountType => {
    this.setState({ account: { accountType: parseInt(accountType) }});
  };

  render() {
    return (
      <form>
        <h3>Usuario</h3>

        <div className="row">
          <div className="col-md-6">
            <div className={validation.user.email.isInvalid && 'has-error'}>
              <label htmlFor="user.email">Correo Electronico</label>
              <input type="email" className="form-control"
                     name="user.email"
                     placeholder="john@doe.com"
                     onChange={this.handleInputChange}
              />
              <span className="help-block">{validation.user.email.message}</span>
            </div>
          </div>
          <div className="col-md-6">
            <div className={validation.user.name.isInvalid && 'has-error'}>
              <label htmlFor="user.name">Nombre</label>
              <input type="text" className="form-control"
                     name="user.name"
                     placeholder="Nombre"
                     onChange={this.handleInputChange}
              />
              <span className="help-block">{validation.user.name.message}</span>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-md-6">
            <div className={validation.user.password.isInvalid && 'has-error'}>
              <label htmlFor="user.password">Contraseña</label>
              <input type="password" className="form-control"
                     name="user.password"
                     onChange={this.handleInputChange}
              />
              <span className="help-block">{validation.user.password.message}</span>
            </div>
          </div>
          <div className="col-md-6">
            <div className={validation.user.password_confirmation.isInvalid && 'has-error'}>
              <label htmlFor="user.password_confirmation">Confirme su contraseña</label>
              <input type="password" className="form-control"
                     name="user.password_confirmation"
                     onChange={this.handleInputChange}
              />
              <span className="help-block">{validation.user.password_confirmation.message}</span>
            </div>
          </div>
        </div>

        <h3>Informacion Personal</h3>

        <div className="row">
          <div className="col-md-6">
            <div className={validation.account.name.isInvalid && 'has-error'}>
              <label htmlFor="account.name">Nombre</label>
              <input type="text" className="form-control"
                     name="account.name"
                     placeholder="Juan Gomez"
                     onChange={this.handleInputChange}
              />
              <span className="help-block">{validation.account.name.message}</span>
            </div>
          </div>
          <div className="col-md-6">
            <div className={validation.account.email.isInvalid && 'has-error'}>
              <label htmlFor="account.email">Correo Electronico</label>
              <input type="email" className="form-control"
                     name="account.email"
                     placeholder="john@doe.com"
                     onChange={this.handleInputChange}
              />
              <span className="help-block">{validation.account.email.message}</span>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-md-6">
            <div className={validation.account.phone.isInvalid && 'has-error'}>
              <label htmlFor="account.phone">Telefono</label>
              <input type="phone" className="form-control"
                     name="account.phone"
                     placeholder="(xxx)xxx-xxxx"
                     onChange={this.handleInputChange}
              />
              <span className="help-block">{validation.account.phone.message}</span>
            </div>
          </div>
          <div className="col-md-6">
            <AccountType onAccountTypeChange={this.handleAccountType} />
          </div>
        </div>

        <h3>Condominio</h3>
        <div className="row">
          <div className="col-md-6">
            <div className={validation.condo.name.isInvalid && 'has-error'}>
              <label htmlFor="condo.name">Nombre</label>
              <input type="text" className="form-control"
                     name="condo.name"
                     placeholder="Nombre"
                     onChange={this.handleInputChange}
              />
              <span className="help-block">{validation.condo.name.message}</span>
            </div>
          </div>
          <div className="col-md-6">
            <div className={validation.units.qty.isInvalid && 'has-error'}>
              <label htmlFor="units.qty">Unidades</label>
              <input type="number" name="units.qty"
                     className="form-control"
                     placeholder="No. de unidades"
                     onChange={this.handleInputChange}
              />
            </div>
          </div>
        </div>
      </form>
    )
  }
  
}